.App {
  display: flex;
  background-color: #2b2b35;
  min-height: 100vh;
  padding: 10px;
  font-family: "Helvetica-Light", Arial, sans-serif;
}
.main {
  width: calc(100% - 140px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
}
.chart {
  font-size: 12px;
  flex-grow: 1;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
}
.filters {
  display: flex;
  flex-direction: column;
  color: white;
  font-size: 12px;
  border-right: 1px solid #616170;
  padding-right: 20px;
}
.menu {
  overflow-y: hidden;
  width: 140px;
  max-height: 180px;
  margin-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
}
.item {
  background-color: #616170;
  border-bottom: 1px solid #2b2b35;
  border-right: 1px solid #2b2b35;
  box-sizing: border-box;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px;
  width: 100%;
  cursor: pointer;
  text-align: center;
  transition: 50ms background-color ease-out;
}
.item.selected {
  background-color: #2b2b35;
}
.item.empty {
  background-color: #2b2b35;
  border-bottom: 1px solid #616170;
  color: #616170;
  cursor: inherit;
  font-size: 10px;
}
.remove {
  cursor: pointer;
  height: 100%;
  width: 35px;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  color: hsl(0, 0%, 80%);
}
.remove:hover {
  background-color: #515158;
}
.remove > div {
  width: 100%;
  font-size: 18px;
  border-left: 1px solid hsl(0, 0%, 80%);
}
.country {
  cursor: inherit;
  padding: 0;
  justify-content: space-between;
  border-bottom: 1px solid #616170;
}
.countryName {
  flex-grow: 1;
}
.tour:hover {
  background-color: #a7dee1;
}
.tour.selected {
  background-color: #3dadb2;
}
.tournament:hover {
  background-color: #e9a1d5;
}
.tournament.selected {
  background-color: #C32E9A;
}
.round:hover {
  background-color: #f9bc8e;
}
.round.selected {
  background-color: #F3832E;
}
.round,
.tour {
  display: flex;
  flex-wrap: wrap;
  width: 50%;
}
.country-input #select {
  width: 100%;
  height: 36px;
}
.title {
  height: 50px;
  display: flex;
  font-size: 30px;
  margin-left: 5px;
  font-weight: bold;
  color: white;
  box-sizing: border-box;
}
.title > span {
  width: 50%;
  margin-left: 5px;
  background: #F3832E;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  flex-shrink: 0;
}
.title > div {
  margin-left: 5px;
  box-sizing: border-box;
}
.title > div:nth-child(5) {
  width: 19%;
  opacity: 0.8;
  background: #f59a55;
}
.title > div:nth-child(4) {
  width: 15%;
  opacity: 0.6;
  background: #f7b17b;
}
.title > div:nth-child(3) {
  width: 11%;
  opacity: 0.4;
  background: #fac8a2;
}
.title > div:nth-child(2) {
  width: 7%;
  opacity: 0.3;
  background: #fcdfc8;
}
.title > div:nth-child(1) {
  width: 3%;
  opacity: 0.2;
  background: #fef5ef;
}
.crumbs {
  color: white;
  font-size: 12px;
  margin-left: 56px;
}
.crumbs > span {
  margin: 10px;
}
.crumbs > span:nth-child(1) {
  color: #81d0d4;
}
.crumbs > span:nth-child(2) {
  color: #df78c3;
}
.crumbs > span:nth-child(3) {
  color: #cccccc;
}
.crumbs > span:nth-child(4) {
  color: #f9bc8e;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
